import React from 'react'
import PropTypes from 'prop-types'
import { FormattedMessage } from 'react-intl'
import Layout from '../components/Layout'
import LocalizedLink from '../components/LocalizedLink'
import HeroImage from "../images/wattys_splash_hero.png"
import PanelNews from "../images/wattys_CTA_news.jpg"
import PastWinners from "../images/wattys_CTA_pastWinners.jpg"
import Helmet from "react-helmet";
import { withPrefix, Link } from "gatsby"


const IndexPage = ({ pageContext: { locale } }) => (
  <Layout locale={locale}>


              

    <img src={HeroImage} alt="Hero Image" />

    <p class="light-txt font-med text-center pad-t">
        <FormattedMessage id="coutDownHeader" />
    </p>

    <div class="wrapper-countdown">

        <Helmet>
         <script src={withPrefix('script.js')} type="text/javascript" />
        </Helmet>

        <div>
            <p class="light-txt timer" id="days"></p>
            <p class="bold-txt teal-font "><FormattedMessage id="days" /></p>
        </div>
        <div>
            <p class="light-txt timer" id="hours"></p>
            <p class="bold-txt teal-font"><FormattedMessage id="hours" /></p>       
        </div>
       <div>
          <p class="light-txt timer" id="minutes"></p>
          <p class="bold-txt teal-font"><FormattedMessage id="seconds" /></p>
        </div>
       <div >
         <p class="light-txt timer" id="seconds"></p>
         <p class="bold-txt teal-font"><FormattedMessage id="minutes" /></p>
       </div>
    </div>

    <div class="divider"></div>
 
    <p class="light-txt font-small text-center pad-b">
        <FormattedMessage id="paragraphOne" />
    </p>

    <p class="regular-txt font-small text-center">
         <FormattedMessage id="paragraphTwo" />
    </p>

    <div class="divider"></div>


    <div class="panel-wrapper pad-xb">
        <div class="panel-inner">
            <div class="panel">
                <a href="https://www.wattpad.com/user/TheWattys" target="_blank">
                    <img src={PanelNews} alt="News" />
                    <p class="bold-txt"><FormattedMessage id="wattyNewsHeading" /></p>
                    <p class="light-txt"><FormattedMessage id="wattyNewsCopy" /></p>
                </a>
            </div>
        </div>
        <div class="panel-inner">
            <div class="panel">
                <a href="#" target="_blank">
                    <img src={PastWinners} alt="Past Winners" />
                    <p class="bold-txt"><FormattedMessage id="pastWinnersHeading" /></p>
                    <p class="light-txt"><FormattedMessage id="pastWinnersCopy" /></p>
                </a>
            </div>
        </div>
    </div>

  </Layout>
)

IndexPage.propTypes = {
  pageContext: PropTypes.shape({
    locale: PropTypes.string.isRequired
  }).isRequired
}

export default IndexPage

